    h1 What does CALDERA™ do?
    p CALDERA helps cybersecurity professionals reduce the amount of time and resources needed for routine cybersecurity testing. 
    p CALDERA empowers cyber teams in three main ways:
    .use-case.left
      h2 Autonomous Adversary Emulation
      p With CALDERA, your cyber team can build a specific threat (adversary) profile and launch it in a network to see where you may be susceptible. This helps with testing defenses and training blue teams on how to detect specific threats.
    .use-case.right
      h2 Autonomous Incident Response
      p  Enables your team to perform automated incident response on a given host, allowing them to find new ways to identify and respond to threats.
    .use-case.left
      h2 Manual Red-Team Engagements
      p  Helps your red team perform manual assessments with computer assistance by augmenting existing offensive toolsets. The framework can be extended with any custom tools you may have.

